* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.4s;
  &:hover {
    background-color: grey;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  width: 100%;
}
